/*全局公用css*/

body,
html {
  font-family: "Microsoft YaHei", 微软雅黑;
  color: #333;
  background: #f3f3f3;
  padding: 0;
  margin: 0 auto;
  height: 100%;
}
a{
  color: #333;
}
dl,
dd,
ol,
ul,
p,
h4 {
  padding: 0;
  margin: 0;
}

li, dd, dt {
  list-style-type: none;
}

i,em {
  font-style: normal;
}

a {
  text-decoration: none;
  out-line: none
}

ul {
  list-style: none;
}

img {
  vertical-align: middle;
  border-style: none;
}

.fl {
  float: left;
}

.fr {
  float: right;
}

.pa {
  position: absolute;
}

.pr {
  position: relative;
}

.pf {
  position: fixed;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
}

.mb10 {
  margin-bottom: 10px;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/

::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background-color: #F5F5F5;
}

/*定义滚动条轨道 内阴影+圆角*/

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px rgba(0, 0, 0, 0);
  border-radius: 10px;
  background-color: #F5F5F5;
}

/*定义滑块 内阴影+圆角*/

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
  background-color: #555;
}

input[type="button"],
input[type="submit"],
input[type="search"],
input[type="reset"] {
  -webkit-appearance: none;
}

textarea {
  -webkit-appearance: none;
  resize: none;
}

.clearfix:after {
  content: "";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
  font-size: 0;
}

.clearfix {
  *zoom: 1;
}

.nowrap {
  white-space: nowrap;
}

.address:after {
  content: '';
  display: inline-block;
  background: url("../../../static/images/address.png") no-repeat;
  background-size: 60%;
  width: 14px;
  height: 6px;
}

.wdj:after {
  content: '';
  background: url("../../../static/images/wdj.png") no-repeat;
  display: inline-block;
  width: 15px;
  height: 19px;
  background-size: 60%;
  vertical-align: middle;
}

.sx:after {
  content: '';
  display: inline-block;
  width: 15px;
  height: 12px;
  background: url("../../../static/images/sx.png") no-repeat;
  background-size: 75%;
}

/*字体价格样式*/

.price {
  color: #f67476;
  font-size: 16px;
  font-weight: bold;
}

/*主色*/
.cbe6 {
  background: #e66280;
}

.ce6 {
  color: #e66280;
}

.c3 {
  color: #333;
}

.c6 {
  color: #666;
}

.c9 {
  color: #999;
}
.f13{
  font-size: 13px;
}
.f15{
  font-size: 15px;
}
.f16{
  font-size: 16px;
}
/*溢出变省略号*/

.te {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.tc {
  text-align: center;
}


