/*底部购买*/
.footer_box[data-v-5e8ebb94] {
  width: 100%;
  height: 49px;
  position: fixed;
  bottom: 0px;
  left: 0;
  z-index: 999;
}
.footer_box .footer_l[data-v-5e8ebb94] {
  text-align: center;
  background: #fff;
  height: 49px;
  line-height: 14px;
  border-right: 1px solid #ccc;
  width: 16.66%;
  float: left;
}
.footer_box .no_sc .coll[data-v-5e8ebb94] {
  width: 20px;
  height: 20px;
  display: inline-block;
  background: url(../../static/images/coll-no.png) no-repeat;
  background-size: 100%;
  margin-top: 8px;
}
.footer_box .no_sc p[data-v-5e8ebb94] {
  color: #b2b2b2;
}
.footer_box .ok_sc .coll[data-v-5e8ebb94] {
  width: 20px;
  height: 20px;
  display: inline-block;
  background: url(../../static/images/coll-yes.png) no-repeat;
  background-size: 100%;
  margin-top: 8px;
}
.footer_box .ok_sc p[data-v-5e8ebb94] {
  color: #e66280;
}
.footer_box .footer_l > a > p[data-v-5e8ebb94] {
  font-size: 10px;
}
.footer_box .footer_c[data-v-5e8ebb94] {
  text-align: center;
  background: #fff;
  height: 49px;
  padding-right: 15px;
  padding-left: 15px;
  border-right: 1px solid #ccc;
