}
/*收藏成功，取消收藏*/
.addbox[data-v-5e8ebb94],
.delbox[data-v-5e8ebb94] {
  position: fixed;
  background: #000;
  opacity: 0.5;
  top: 30%;
  left: 50%;
  padding: 5px;
  width: 80px;
  margin-left: -40px;
  z-index: 333;
  color: #fff;
  text-align: center;
  border-radius: 3px;
}



